html, body, div, span, h1, h2, h3, h4, h5, h6, p,
a, em, strong, ol, ul, li, form, footer, header,
menu, nav {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

* {
    box-sizing: border-box;
}

html{
    background-color: rgb(0, 86, 161);
}

.scoreBoard {
    display: flex;
    flex-direction: row;
    height: 15vh;
    background-color: rgb(203, 225, 255);
    justify-content: center;
    align-items: center;
    color: rgb(48, 48, 48);
}

.currentScore, .highScore {
    font-size: 2rem;
    margin: 0 2rem 0 2rem;
}

.cardsContainer{
    display: grid;
    grid-template-columns: repeat(8, 1fr [col-start]);
    grid-template-rows: repeat(1, 1fr [col-start]);;
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3rem;
}

.card{
    cursor: pointer;
    margin: .5rem;
}

.teamName{
    text-align: center;
    color: aliceblue;
}

footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 8vh;
    color: white;
    text-align:center;
    background-color: rgb(0, 33, 79);
    font-size: 1.25rem;
    padding-top: 1.2rem;
 }